import React from "react";
import { Container, Row, Col } from "react-bootstrap";
import { Link } from "react-router-dom";
import logo from "../logo.png"; // Import your logo

function Footer() {
  return (
    <footer style={{ backgroundColor: "#0D1117", color: "#ffffff", padding: "50px 0", borderTopLeftRadius: "20px", borderTopRightRadius: "20px" }}>
      <Container>
        <Row className="text-center text-md-start align-items-center">
          {/* Logo Section */}
          <Col md={4} className="mb-4 text-center text-md-start">
            <img src={logo} alt="Otaku Shop" style={{ width: "150px", height: "auto" }} />
          </Col>

          {/* Navigation Links */}
          <Col md={4} className="mb-4 text-center">
            <ul className="list-unstyled d-flex justify-content-center gap-4">
              <li>
                <Link to="/shop" className="text-decoration-none fw-bold" style={{ color: "#ffffff", fontSize: "16px" }}>Shop</Link>
              </li>
              <li>
                <Link to="/features" className="text-decoration-none fw-bold" style={{ color: "#ffffff", fontSize: "16px" }}>Categories</Link>
              </li>
              <li>
                <Link to="/pricing" className="text-decoration-none fw-bold" style={{ color: "#ffffff", fontSize: "16px" }}>Contact Us</Link>
              </li>
            </ul>
          </Col>

          {/* Social Media Icons */}
          <Col md={4} className="mb-4 text-center text-md-end">
            <div className="d-flex justify-content-center justify-content-md-end gap-3">
              <a href="https://twitter.com" target="_blank" rel="noopener noreferrer">
                <i className="fab fa-twitter" style={{ color: "#ffffff", fontSize: "20px", padding: "10px", borderRadius: "50%", backgroundColor: "#161B22" }}></i>
              </a>
              <a href="https://facebook.com" target="_blank" rel="noopener noreferrer">
                <i className="fab fa-facebook-f" style={{ color: "#ffffff", fontSize: "20px", padding: "10px", borderRadius: "50%", backgroundColor: "#161B22" }}></i>
              </a>
              <a href="https://instagram.com" target="_blank" rel="noopener noreferrer">
                <i className="fab fa-instagram" style={{ color: "#ffffff", fontSize: "20px", padding: "10px", borderRadius: "50%", backgroundColor: "#161B22" }}></i>
              </a>
              <a href="https://github.com" target="_blank" rel="noopener noreferrer">
                <i className="fab fa-github" style={{ color: "#ffffff", fontSize: "20px", padding: "10px", borderRadius: "50%", backgroundColor: "#161B22" }}></i>
              </a>
            </div>
          </Col>
        </Row>

        {/* Divider Line */}
        <hr style={{ borderTop: "1px solid #374151", opacity: "0.3" }} />

        {/* Copyright Section */}
        <Row>
          <Col className="text-center py-3">
            &copy; {new Date().getFullYear()} All Rights Reserved
          </Col>
        </Row>
      </Container>
    </footer>
  );
}

export default Footer;
