import React, { useEffect, useState } from "react";
import { Row, Col, Card } from "react-bootstrap";
import { Link } from "react-router-dom";
import "../css/category.css"; // Custom CSS for styling

function CategoryPage() {
  // Static category data with image URLs (can be replaced with dynamic API data)
  const [categories, setCategories] = useState([
    { name: "Figures", image: "../media/images/categories/shirt.jpg" },
    { name: "Manga & Video", image: "../media/images/categories/kitchen.jpg" },
    { name: "Accessories", image: "../media/images/categories/acessories.jpg" },
    { name: "Clothing", image: "../media/images/categories/clothes.jpg" },
    { name: "Electronics", image: "../media/images/categories/electronics.jpg" },
  ]);

  // Fetch categories (if you're fetching from an API)
  // useEffect(() => {
  //   fetch('/api/categories')
  //     .then(response => response.json())
  //     .then(data => setCategories(data.categories))
  // }, []);

  return (
    <div className="category-page">
      <h1 className="text-center my-4">Product Categories</h1>
      <Row>
        {categories.map((category, index) => (
          <Col key={index} sm={12} md={6} lg={4} xl={3} className="mt-3">
            <Card className="category-card">
              <Link to={`/category/${category.name.toLowerCase()}`}>
                <div className="category-image-wrapper">
                  <Card.Img
                    variant="top"
                    src={category.image}
                    alt={category.name}
                    className="category-image"
                  />
                </div>
                <Card.Body className="text-center">
                  <Card.Title className="category-title">{category.name}</Card.Title>
                  <Card.Text className="category-description">
                    Browse products in {category.name}.
                  </Card.Text>
                </Card.Body>
              </Link>
            </Card>
          </Col>
        ))}
      </Row>
    </div>
  );
}

export default CategoryPage;
