import React from "react";
import { Navbar, Nav, Container, NavDropdown } from "react-bootstrap";
import { LinkContainer } from "react-router-bootstrap";
import { useDispatch, useSelector } from "react-redux";
import { logout } from "../actions/userActions";
import SearchBox from "./SearchBox";
import logo from "../logo.png"; // Logo image

function Header() {
  const dispatch = useDispatch();
  const userLogin = useSelector((state) => state.userLogin);
  const { userInfo } = userLogin;

  const logoutHandler = () => {
    dispatch(logout());
  };

  return (
    <header>
      <Navbar
        expand="lg"
        collapseOnSelect
        className="px-3"
        style={{
          backgroundColor: "#1F2937", // Dark Grayish-Blue
          padding: "12px 0",
        }}
      >
        <Container>
          {/* Logo - Large & Clear */}
          <LinkContainer to="/">
            <Navbar.Brand>
              <img
                src={logo}
                alt="Otaku Shop"
                style={{
                  width: "130px", // Larger
                  height: "auto",
                  objectFit: "contain",
                }}
              />
            </Navbar.Brand>
          </LinkContainer>

          {/* Toggle Button for Mobile */}
          <Navbar.Toggle
            aria-controls="navbarScroll"
            style={{ backgroundColor: "#ffffff" }}
          />

          <Navbar.Collapse id="navbarScroll">
            {/* Navigation Tabs */}
            <Nav className="me-auto">
              <LinkContainer to="/shop">
                <Nav.Link
                  className="text-light"
                  style={{ fontSize: "16px", transition: "0.3s" }}
                >
                  Shop
                </Nav.Link>
              </LinkContainer>
              <LinkContainer to="/categories">
                <Nav.Link
                  className="text-light"
                  style={{ fontSize: "16px", transition: "0.3s" }}
                >
                  Categories
                </Nav.Link>
              </LinkContainer>
              {/* <LinkContainer to="/offers">
                <Nav.Link
                  style={{
                    color: "#ffffff",
                    fontSize: "16px",
                    fontWeight: "bold",
                    transition: "0.3s",
                  }}
                >
                  🔥 Offers
                </Nav.Link>
              </LinkContainer> */}
              <LinkContainer to="/contact">
                <Nav.Link
                  className="text-light"
                  style={{ fontSize: "16px", transition: "0.3s" }}
                >
                  Contact
                </Nav.Link>
              </LinkContainer>
            </Nav>

            {/* Search Box - Centered on Large Screens */}
            <div className="ms-auto me-auto d-none d-lg-block">
              <SearchBox />
            </div>

            {/* User & Cart Icons */}
            <Nav className="ms-auto">
              <LinkContainer to="/cart">
                <Nav.Link
                  className="d-flex align-items-center text-light"
                  style={{ fontSize: "16px", transition: "0.3s" }}
                >
                  <i className="fas fa-shopping-cart me-1"></i> Cart
                </Nav.Link>
              </LinkContainer>

              {userInfo ? (
                <NavDropdown
                  title={
                    <i
                      className="fas fa-user text-light"
                      style={{ fontSize: "18px" }}
                    ></i>
                  }
                  id="username"
                  className="text-light"
                >
                  <LinkContainer to="/profile">
                    <NavDropdown.Item>Profile</NavDropdown.Item>
                  </LinkContainer>
                  <NavDropdown.Item onClick={logoutHandler}>Logout</NavDropdown.Item>
                </NavDropdown>
              ) : (
                <LinkContainer to="/login">
                  <Nav.Link
                    className="d-flex align-items-center text-light"
                    style={{ fontSize: "16px", transition: "0.3s" }}
                  >
                    <i className="fas fa-sign-in-alt me-1"></i> Login
                  </Nav.Link>
                </LinkContainer>
              )}

              {/* Admin Menu */}
              {userInfo && userInfo.isAdmin && (
                <NavDropdown
                  title="Admin"
                  id="adminmenu"
                  className="text-light"
                >
                  <LinkContainer to="/admin/userlist">
                    <NavDropdown.Item>Users</NavDropdown.Item>
                  </LinkContainer>
                  <LinkContainer to="/admin/productlist">
                    <NavDropdown.Item>Products</NavDropdown.Item>
                  </LinkContainer>
                  <LinkContainer to="/admin/orderlist">
                    <NavDropdown.Item>Orders</NavDropdown.Item>
                  </LinkContainer>
                </NavDropdown>
              )}
            </Nav>
          </Navbar.Collapse>
        </Container>

        {/* Search Box for Mobile View */}
        <div className="d-block d-lg-none px-3 mt-2">
          <SearchBox />
        </div>
      </Navbar>
    </header>
  );
}

export default Header;
