import React, { useEffect } from "react";
import { Container, Row, Col } from "react-bootstrap"; // Import Container
import Product from "../components/Product";
import LatestProduct from "../components/LatestProduct";
import Loader from "../components/Loader";
import Message from "../components/Message";
import Paginate from "../components/Paginate";
import ProductCarousel from "../components/ProductCarousel";
import Slider from "../components/Slider";
import { useDispatch, useSelector } from "react-redux";
import { listLatestProducts } from "../actions/productActions";
import "../css/HomePage.css"; // Import the updated CSS

function HomeScreen({ history }) {
  const dispatch = useDispatch();

  const productLatest = useSelector((state) => state.productLatest);
  const { products, loading, error } = productLatest;

  let keyword = history.location.search;

  useEffect(() => {
    dispatch(listLatestProducts());
  }, [dispatch]);

  return (
    <div className="home-screen">
      {/* Full-width Slider */}
      {!keyword && <Slider />}

      {/* Wrap products and carousel inside a Container */}
      <Container>
        <h1 className="text-center my-4">Latest Products</h1>

        {loading ? (
          <Loader />
        ) : error ? (
          <Message variant="danger">{error}</Message>
        ) : (
          <>
            {/* Product Grid */}
            <Row>
              {products.map((product) => (
                <Col key={product._id} sm={12} md={6} lg={3} xl={3}>
                  <div className="product-item">
                    <LatestProduct product={product} />
                  </div>
                </Col>
              ))}
            </Row>

            {/* Pagination */}
            {/* <div className="pagination-container">
              <Paginate page={page} pages={pages} keyword={keyword} />
            </div> */}

            {/* Product Carousel inside Container */}
            <h2 className="text-center fw-bold py-5">Top Rated Products</h2>
            <ProductCarousel />
          </>
        )}
      </Container>
    </div>
  );
}

export default HomeScreen;
