import React from "react";
import { Carousel, Button } from "react-bootstrap";
import { Link } from "react-router-dom";

const slides = [
  {
    id: 1,
    title: "Exclusive Deals on Electronics",
    description: "Get up to 50% off on the latest gadgets and accessories!",
    image: "../media/images/slider1.jpg",
    link: "/shop",
  },
  {
    id: 2,
    title: "Trendy Fashion Sale",
    description: "Discover the latest fashion trends at unbeatable prices.",
    image: "../media/images/slider4.jpg",
    link: "/shop",
  },
  {
    id: 3,
    title: "Upgrade Your Home Essentials",
    description: "Top-quality home products with amazing discounts!",
    image: "../media/images/slider5.jpg",
    link: "/shop",
  },
];

function HomeSlider() {
  return (
    <Carousel className="custom-slider" interval={5000} pause="hover" fade>
      {slides.map((slide) => (
        <Carousel.Item key={slide.id}>
          <img className="d-block w-100 slider-image" src={slide.image} alt={slide.title} />
          <Carousel.Caption className="slider-caption">
            <h2>{slide.title}</h2>
            <p>{slide.description}</p>
            <Link to={slide.link}>
              <Button variant="primary" size="lg">
                Shop Now
              </Button>
            </Link>
          </Carousel.Caption>
        </Carousel.Item>
      ))}
    </Carousel>
  );
}

export default HomeSlider;
